import Cart from '../components/cart/Cart'
import axios from 'axios'
axios.defaults.withCredentials=true;
const cart=()=>{
    var id
    axios.get('http://localhost:3002/cookie').then(response=>{
        id=response.data  
    })
    console.log(id)
    return(
        <div><Cart/></div>
    )
}
export default cart


